import React, { useEffect, useState } from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import SubmissionBar from '../features/submissionbar/SubmissionBar';
import { db } from '../services/firebase';
const useStyles = makeStyles({
    title: {
        fontFamily: "'Train One', cursive",
        // color: "white",
    },
    heroBackground: {
        width: "100%",
        margin: 0,
        textAlign: "center",
        height: "500px",
        paddingTop: "120px",
        boxSizing: "border-box",
        height: "100vh",
        // background: "gray url(https://www.toptal.com/designers/subtlepatterns/patterns/moroccan-flower-dark.png) repeat 0 0",
        // animation: "slide 20s linear infinite",
    },
    greenIndicator: {
        background: "#28B62C",
        display: "inline-block",
        width: "1em",
        height: "1em",
        borderRadius: "50%",
        // -webkit-animation: pulse-animation 2s infinite linear;
        animation: `$pulse 2s infinite linear`,
    },
    redIndicator: {
        background: "red",
        display: "inline-block",
        width: "1em",
        height: "1em",
        borderRadius: "50%",

    },
    "@keyframes pulse": {
        "0%": { transform: "scale(1)" },
        "25%": { transform: "scale(1)" },
        "50%": { transform: "scale(1.2)" },
        "75%": { transform: "scale(1)" },
        "100%": { transform: "scale(1)" },
    }
});

export default function Hero() {
    const classes = useStyles();
    // need to dispatch status and selector for status 
    const [status, setStatus] = useState("offline");

    useEffect(() => {
        var ref = db.ref("status");
        ref.on('value', (snapshot) => {
            console.log('status: ' + snapshot.val());
            setStatus(snapshot.val());
        }, (errorObject) => {
            console.log('The read failed: ' + errorObject.name);
        });
    }, []);
    return (
        <>
            <div style={{ display: "flex", justifyContent: "flex-end", alignItems: "center", margin: "16px" }}>
                {status === "online"
                    ? (<span className={classes.greenIndicator}></span>)
                    : (<span className={classes.redIndicator}></span>)}
                <Typography className={classes.title} style={{ marginLeft: "8px" }} variant="subtitle2" gutterBottom>
                    {status}
                </Typography>
            </div>
            <div className={classes.heroBackground}>
                <Typography className={classes.title} variant="h2" gutterBottom>
                    Page Saver
            </Typography>
                <div></div>
                <SubmissionBar />
            </div >
        </>
    );
}